<template>
    <div class="container-fluid wrapper">
    <h2>Forum Dreamer-online</h2>
    <div>
        <h3 class="group-header">Основная категория</h3>
        <div class="group-topics">
            <ul>
                <li class="themegroup" @click="routerTest('news')">Новости и объявления</li>
                <li class="themegroup" @click="routerTest('information')">Полезная информация</li>
                <li class="themegroup" @click="routerTest('promotions')">Акции и мероприятия</li>
                <li class="themegroup" @click="routerTest('qas')">Вопросы и ответы</li>
            </ul>
        </div>

        <h3 class="group-header">Общение</h3>
        <div class="group-topics">
            <ul>
                <li class="themegroup" @click="routerTest('tavern')">Беседка</li>
                <li class="themegroup" @click="routerTest('clans')">Группы и кланы</li>
                <li class="themegroup" @click="routerTest('marketplace')">Рынок</li>
            </ul>
        </div>

        <h3 class="group-header">Технический раздел</h3>
        <div class="group-topics">
            <ul>
                <li class="themegroup" @click="routerTest('support')">Поддержка игроков</li>
                <li class="themegroup" @click="routerTest('bugs')">Недоработки и баги</li>
            </ul>
        </div>

    </div>
    </div>
</template>

<script>
    export default {
        name: 'app',
        data() {
            return {

            }
        },
        methods: {
            routerTest(themegroup) {
                this.$router.push({name: 'themegroup', params:{Tid:themegroup}});
            }
        }
    }
</script>
   

<style>
    .themegroup {
        cursor: pointer;
        list-style-type: none;
    }
    h2, h3 {
        text-shadow: 0px 3px 3px white;
    }
    a {
        text-decoration: none;
        color: white;
        text-shadow: 0px 3px 3px black;
    }
</style>


